import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import dayjs from 'dayjs';

// MUI Icons
import VerifiedUser from '@material-ui/icons/VerifiedUser';

const styles = (theme) => ({
	...theme.spreadThis,
	container: {
		margin: 'auto',
	},
	description: {
		margin: '1.071rem auto 0',
		marginBottom: '30px',
		maxWidth: '600px',
		color: '#999',
		textAlign: 'center !important',
	},
	fecha: {
		margin: '1.071rem auto 0',
		maxWidth: '600px',
		color: '#999',
		textAlign: 'center !important',
	},
	image: {
		width: '100%',
	},
	block: {
		color: 'inherit',
		fontWeight: '500',
		fontSize: '12px',
		textTransform: 'uppercase',
		borderRadius: '3px',
		textDecoration: 'none',
	},
});

class Evento extends Component {
	render() {
		const {
			classes,
			evento: {
				titulo,
				descripcion,
				imagen,
				fecha = '2020',
				link = 'https://www.google.com',
			},
		} = this.props;
		return (
			<div style={{ marginTop: '20px' }}>
				<img src={imagen} alt={titulo} className={classes.image} />
				<div className={classes.container}>
					<a
						href={link}
						className={classes.block}
						target='_blank'
						rel='noopener noreferrer'
					>
						<h3 style={{ textAlign: 'center' }}>
							<VerifiedUser />
							{titulo}
						</h3>
					</a>

					<h4 className={classes.fecha}>
						Entrega de Papers: {dayjs(fecha).format('DD-MM-YYYY')}
					</h4>
					<p className={classes.description}>{descripcion}</p>
				</div>
			</div>
		);
	}
}

export default withStyles(styles)(Evento);
