import React from 'react';

import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';

export default ({
	children,
	onClick,
	tip,
	btnClassName,
	tipClassName,
	url
}) => (
	<Tooltip title={tip} className={tipClassName} placement="bottom">
		<IconButton
			onClick={onClick}
			className={btnClassName}
			href={url}
			target="_blank"
		>
			{children}
		</IconButton>
	</Tooltip>
);
