import React, { Component } from 'react';
// nodejs library that concatenates classes
import classNames from 'classnames';
// nodejs library to set properties for components
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';

// @material-ui/core components
import { withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

// core components
//import GridContainer from 'components/Grid/GridContainer.js';
//import GridItem from 'components/Grid/GridItem.js';
import Taller from './Taller';

import styles from 'assets/jss/material-kit-react/components/navPillsStyle.js';

class NavCAS extends Component {
	state = {
		active: 0,
	};

	handleChange = (event, newValue) => {
		this.setState({
			active: newValue,
		});
	};

	handleChangeIndex = (index) => {
		this.setState({
			active: index,
		});
	};

	render() {
		const { active } = this.state;
		const { classes, direction, color, info, alignCenter } = this.props;

		const flexContainerClasses = classNames({
			[classes.flexContainer]: true,
		});

		const tabButtons = (
			<Tabs
				classes={{
					root: classes.root,
					fixed: classes.fixed,
					flexContainer: flexContainerClasses,
					indicator: classes.displayNone,
				}}
				value={active}
				onChange={this.handleChange}
				centered={alignCenter}
			>
				{info.map((taller, indice) => {
					const pillsClasses = classNames({
						[classes.pills]: true,
						[classes.pillsWithIcons]: taller.tabIcon !== undefined,
					});
					return (
						<Tab
							label={taller.titulo}
							key={indice}
							classes={{
								root: pillsClasses,
								selected: classes[color],
								wrapper: classes.tabWrapper,
							}}
						/>
					);
				})}
			</Tabs>
		);

		const tabContent = (
			<div className={classes.contentWrapper}>
				<SwipeableViews
					axis={direction === 'rtl' ? 'x-reverse' : 'x'}
					index={active}
					onChangeIndex={this.handleChangeIndex}
				>
					{info.map((taller, indice) => {
						return <Taller info={taller} key={indice} />;
					})}
				</SwipeableViews>
			</div>
		);

		return (
			<div>
				{tabButtons}
				{tabContent}
			</div>
		);
	}
}

NavCAS.defaultProps = {
	active: 0,
	color: 'primary',
};

NavCAS.propTypes = {
	active: PropTypes.number,
	color: PropTypes.oneOf([
		'primary',
		'warning',
		'danger',
		'success',
		'info',
		'rose',
	]),
	direction: PropTypes.string,
	alignCenter: PropTypes.bool,
};

export default withStyles(styles)(NavCAS);
