import { SET_EVENTOS, SET_TALLERES, LOADING_DATA } from './types';
import axios from 'axios';

// Get all screams
export const leeEventos = () => dispatch => {
	dispatch({ type: LOADING_DATA });
	axios
		.get('/eventos')
		.then(res => {
			dispatch({
				type: SET_EVENTOS,
				payload: res.data
			});
		})
		.catch(() => {
			dispatch({
				type: SET_EVENTOS,
				payload: []
			});
		});
};

export const leeTalleres = () => dispatch => {
	dispatch({ type: LOADING_DATA });
	axios
		.get('/talleres')
		.then(res => {
			dispatch({
				type: SET_TALLERES,
				payload: res.data
			});
		})
		.catch(err => {
			dispatch({
				type: SET_TALLERES,
				payload: []
			});
		});
};
