import React, { Component } from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';

// core components
import Header from 'components/Header/Header.js';
import Footer from 'components/Layout/Footer.js';
import GridContainer from 'components/Grid/GridContainer.js';
import GridItem from 'components/Grid/GridItem.js';
import Button from 'components/CustomButtons/Button.js';
import HeaderLinks from 'components/Header/HeaderLinks.js';
import Parallax from 'components/Parallax/Parallax.js';

import styles from 'assets/jss/material-kit-react/views/landingPage.js';

import { connect } from 'react-redux';
import { leeEventos, leeTalleres } from 'redux/dataActions';

import Conferencia from './Conferencia';
import NavCAS from './NavCAS';

class CASPage extends Component {
	componentDidMount() {
		this.props.leeEventos();
		this.props.leeTalleres();
	}
	render() {
		const { classes, ...rest } = this.props;
		const { eventos, talleres, loading } = this.props.data;

		let componenteTalleres = !loading ? (
			<NavCAS alignCenter color='primary' info={talleres} />
		) : (
			<h4 style={{ color: '#999999' }}> Cargando </h4>
		);

		let componenteConferencias = !loading ? (
			eventos.map((evento, indice) => (
				<Conferencia evento={evento} key={indice} />
			))
		) : (
			<h4 style={{ color: '#999999' }}>Cargando </h4>
		);

		return (
			<div>
				<Header
					color='transparent'
					brand='Sistemas Electrónicos'
					rightLinks={<HeaderLinks />}
					fixed
					changeColorOnScroll={{
						height: 400,
						color: 'white',
					}}
					{...rest}
				/>
				<Parallax filter image={require('assets/img/landing-cas.jpg')}>
					<div className={classes.container}>
						<GridContainer>
							<GridItem xs={12} sm={12} md={6}>
								<h1 className={classes.title}>IEEE CAS UTEC</h1>
								<h4>
									Somos estudiantes de UTEC de distintos
									ciclos enfocados en el desarrollo académico
									como ingenieros y la aplicación de diversas
									tecnologías basados en circuitos y sistemas.
									Nuestros objetivos son desarrollar, fomentar
									y motivar la elaboración de nuevos proyectos
									enfocados en el desarrollo de circuitos y
									sistemas. Crear alianzas interdisciplinarias
									para fomentar el desarrollo proyectos de
									investigación. Si estas interesado,
									escríbenos!!
								</h4>
								<br />
								<Button
									color='primary'
									size='lg'
									href='https://www.facebook.com/ieeecasutec'
									target='_blank'
									rel='noopener noreferrer'
								>
									<i className='fas fa-id-badge' />{' '}
									Contáctanos
								</Button>
							</GridItem>
						</GridContainer>
					</div>
				</Parallax>
				<div className={classNames(classes.main, classes.mainRaised)}>
					{/**************************************
						Sección de Talleres
					****************************************/}

					<div className={classes.container2}>
						<div className={classes.section}>
							<GridContainer justify='center'>
								<GridItem xs={12} sm={12} md={8}>
									<h2
										className={classes.cas_title}
										style={{ textAlign: 'center' }}
									>
										Talleres
									</h2>
									<h5 className={classes.description}>
										Estamos ofreciendo estos talleres!
										Cualquier duda contáctate con nosotros.
									</h5>
								</GridItem>
							</GridContainer>

							<div className={classes.cas_container}>
								<GridContainer justify='center'>
									<GridItem
										xs={12}
										sm={12}
										md={8}
										className={classes.cas_nav}
									>
										{componenteTalleres}
									</GridItem>
								</GridContainer>
							</div>
						</div>

						{/**************************************
							Sección de conferencias
						****************************************/}

						<div className={classes.section}>
							<GridContainer justify='center'>
								<GridItem xs={12} sm={12} md={8}>
									<h2
										className={classes.cas_title}
										style={{ textAlign: 'center' }}
									>
										Conferencias
									</h2>
									<h5 className={classes.description}>
										Estas son algunas de las conferencias
										por delante en el campo de circuitos y
										sistemas!
									</h5>
								</GridItem>
							</GridContainer>

							<div className={classes.cas_container}>
								<GridContainer justify='center'>
									<GridItem
										xs={12}
										sm={12}
										md={8}
										className={
											classes.cas_carousel_marginAuto
										}
									>
										{componenteConferencias}
									</GridItem>
								</GridContainer>
							</div>
						</div>
					</div>
				</div>
				<Footer />
			</div>
		);
	}
}

CASPage.propTypes = {
	leeEventos: PropTypes.func.isRequired,
	leeTalleres: PropTypes.func.isRequired,
	data: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
	eventos: state.eventos,
	talleres: state.talleres,
	data: state.data,
});

export default connect(
	mapStateToProps,
	{ leeEventos, leeTalleres }
)(withStyles(styles)(CASPage));
