import { SET_EVENTOS, SET_TALLERES, LOADING_DATA } from './types';

const initialState = {
	eventos: [],
	talleres: [],
	loading: false
};

export default function(state = initialState, action) {
	switch (action.type) {
		case LOADING_DATA:
			return {
				...state,
				loading: true
			};
		case SET_EVENTOS:
			return {
				...state,
				eventos: action.payload,
				loading: false
			};
		case SET_TALLERES:
			return {
				...state,
				talleres: action.payload,
				loading: false
			};

		default:
			return state;
	}
}
