import React, { Component } from 'react';

import dayjs from 'dayjs';

import styles from 'assets/jss/tallerStyle.js';

// MUI stuff
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import withStyles from '@material-ui/core/styles/withStyles';

// Icons
import MyButton from 'components/CustomButtons/MyButton';
import EventNoteIcon from '@material-ui/icons/EventNote';
import FacebookIcon from '@material-ui/icons/Facebook';

class Taller extends Component {
	render() {
		const {
			classes,
			info: {
				titulo,
				descripcion,
				inscripcion,
				fechaEjecucion,
				fb,
				imagen,
			},
		} = this.props;

		return (
			<Card className={classes.card}>
				<CardMedia
					image={imagen}
					title='Imagen'
					className={classes.image}
				/>
				<CardContent className={classes.content}>
					<Typography variant='h4'>{titulo}</Typography>
					<br />
					<Typography variant='body2'>{descripcion}</Typography>

					<Typography variant='body2'>
						Fecha: {dayjs(fechaEjecucion).format('DD-MM-YYYY')}
					</Typography>

					<MyButton tip='Registrarse' url={inscripcion}>
						<EventNoteIcon color='action' />
					</MyButton>
					<MyButton tip='Más información' url={fb}>
						<FacebookIcon color='action' />
					</MyButton>
				</CardContent>
				<div></div>
			</Card>
		);
	}
}

export default withStyles(styles)(Taller);
