import React, { Component } from 'react';

import { createBrowserHistory } from 'history';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';

import { Provider } from 'react-redux';
import store from 'redux/store';

import 'assets/scss/material-kit-react.scss?v=1.8.0';

// pages for this product

import HomePage from 'views/HomePage/HomePage.js';

import FPGAPage from 'views/Areas/FPGAPage';
import MicroPage from 'views/Areas/MicroPage';
import IoTPage from 'views/Areas/IoTPage';
import CASPage from 'views/CAS/CASPage';

import axios from 'axios';

var hist = createBrowserHistory();

axios.defaults.baseURL =
	'https://us-central1-labweb-68a83.cloudfunctions.net/api';

class App extends Component {
	render() {
		return (
			// <MuiThemeProvider theme={theme}></MuiThemeProvider>
			<Provider store={store}>
				<Router history={hist}>
					<Switch>
						<Route exact path='/fpga' component={FPGAPage} />
						<Route
							exact
							path='/microelectronics'
							component={MicroPage}
						/>
						<Route exact path='/iot' component={IoTPage} />
						<Route exact path='/ieee-cas' component={CASPage} />
						<Route path='/' component={HomePage} />
					</Switch>
				</Router>
			</Provider>
		);
	}
}

export default App;
